import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, ToastController, AlertCmp, AlertController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-2FA-1',
  segment: '2FA-1'
})
@Component({
  selector: 'page-2FA-1',
  templateUrl: '2FA-1.html'
})

export class twoFactorAuth1Page {
  code: any;
  constructor(public navCtrl: NavController, public translate: TranslateService, public forgotCtrl: AlertController, public user: UserProvider, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {

    this.translate.setDefaultLang('en');
    this.translate.use('es');
    // this.userinfo();
  }

  //disbale function

  disable() {
    this.translate.get('disable-2FA-page.alert').subscribe((translated: string) => {
      var title = this.translate.instant('disable-2FA-page.alert.title');
      var message = this.translate.instant('disable-2FA-page.alert.message');
      var cancel_btn = this.translate.instant('disable-2FA-page.alert.cancel_btn');
      var confirm_btn = this.translate.instant('disable-2FA-page.alert.confirm_btn');
      var disable_success_toast = this.translate.instant('disable-2FA-page.alert.disable_success_toast');
      var disable_error_toast = this.translate.instant('disable-2FA-page.alert.disable_error_toast');

      let alert = this.forgotCtrl.create({
        title: title,
        message: message,
        buttons: [
          {
            text: cancel_btn,
            role: 'cancel',
            handler: () => {
              console.log('Cancel clicked');
            }
          },
          {
            text: confirm_btn,
            handler: () => {

              this.user.disable2fa(this.code).subscribe(
                data => {
                  console.log(data);

                  let datas = JSON.parse(JSON.stringify(data));
                  let data2 = JSON.parse(datas._body);

                  console.log(data2.success);
                  if (data2.success) {

                    let toast = this.toastCtrl.create({
                      message: disable_success_toast,
                      duration: 3000,
                      position: 'top'
                    });

                    toast.onDidDismiss(() => {
                      console.log('Dismissed toast');
                      localStorage.setItem("2fa", JSON.stringify(0));
                      this.navCtrl.setRoot('page-my-security');

                    });

                    toast.present();


                  } else {

                    let toast = this.toastCtrl.create({
                      message: disable_error_toast,
                      duration: 3000,
                      position: 'top'
                    });

                    toast.onDidDismiss(() => {
                      console.log('Dismissed toast');

                    });

                    toast.present();

                  }
                }, error => {
                  console.log(error.JSON);
                  setTimeout(() => {
                    this.navCtrl.setRoot('page-general_error');
                  }, 1000);
                },
              )

            }
          }
        ]
      });
      alert.present();

    })

  }


}



